export interface Welcome {
    cases:      Case[];
    totalCount: number;
    offset:     number;
    limit:      number;
}

export interface Case {
    id:                  string;
    organizationGroupId: string;
    patient:             Patient;
    provider:            Provider;
    outcomePlan:         OutcomePlan;
    startDate:           string;
    endDate:             string;
    appointments:        null;
    medications:         null;
    state:               State;
    escalations:         Escalation[] | null;
    caseInteractions:    CaseInteraction[];
    createdAt:           string;
    updatedAt:           string;
    createdBy:           string;
    updatedBy:           UpdatedBy;
}

export interface CaseInteraction {
    attemptDate:       string;
    taskSummary:       TaskSummary[];
    caseState:         State;
    escalationOnGroup: null | string;
}

export enum State {
    Active = "ACTIVE",
    Completed = "COMPLETED",
    Critical = "CRITICAL",
    Cancelled = "CANCELLED",
    Draft = "DRAFT",
}

export interface TaskSummary {
    taskId:     string;
    goal:       Goal;
    status:     Status;
    escalation: string[];
}

export enum Goal {
    ApologizeForWarmHandoffFailure = "Apologize for Warm Handoff Failure",
    Authentication = "Authentication",
    CallProvider = "Call Provider",
    EmergencyDisclaimer = "Emergency Disclaimer",
    GreetingAndConversationRequest = "Greeting And Conversation Request",
    PlacePatientOnHold = "Place patient on hold.",
    RecordingDisclaimer = "Recording Disclaimer",
    ScheduleAppointment = "Schedule Appointment",
    ThankPatientAndEndCall = "Thank Patient And End Call",
    ValidateAppointment = "Validate Appointment",
}

export enum Status {
    Completed = "COMPLETED",
    Failed = "FAILED",
    Inprogress = "INPROGRESS",
}

export interface Escalation {
    taskId:        string;
    goal:          Goal;
    escalation:    string[] | null;
    conversations: Conversation[];
    status:        string;
    createdDate:   string;
    resolvedDate:  null;
}

export interface Conversation {
    date:    string;
    message: string;
    sender:  Sender;
    intent:  null | string;
}

export enum Sender {
    Patient = "patient",
    System = "system",
}

export interface OutcomePlan {
    id:   string;
    name: OutcomePlanName;
}

export enum OutcomePlanName {
    TCMApptScheduler10Mins = "TCM - Appt Scheduler - 10 mins",
}

export interface Patient {
    patientId:         string;
    firstName:         FirstName;
    lastName:          string;
    dob:               string;
    gender:            Gender;
    pronoun:           Pronoun;
    phoneNumber:       string;
    mobilePhoneNumber: MobilePhoneNumber;
    postalCode:        string;
}

export enum FirstName {
    Mila = "Mila",
    Shailu = "Shailu",
}

export enum Gender {
    Female = "Female",
    Male = "Male",
}

export enum MobilePhoneNumber {
    The16507414956 = "+1 (650) 741-4956",
    The18479973596 = "+1 (847) 997-3596",
}

export enum Pronoun {
    He = "he",
    She = "she",
}

export interface Provider {
    providerId:     string;
    title:          Title;
    name:           ProviderName;
    specialization: Specialization;
    organization:   Organization;
}

export enum ProviderName {
    BeWellDischarger = "BeWell Discharger",
    ShailuDischarge = "Shailu Discharge",
}

export interface Organization {
    organizationId:      string;
    name:                OrganizationName;
    address:             Address;
    phoneNumber:         PhoneNumber;
    preferredHours:      PreferredHours;
    organizationGroupId: string;
}

export enum Address {
    The123AzaleaLnSANFranciscoCA = "123 Azalea Ln, San Francisco, CA",
    The123AzaleaLnSeattleWA = "123 Azalea Ln, Seattle, WA",
}

export enum OrganizationName {
    BeWellSTestOrganization = "BeWell's Test Organization",
    ShailuSTestOrganization = "Shailu's Test Organization",
}

export enum PhoneNumber {
    The6504659475 = "(650) 465-9475",
    The8474978046 = "(847) 497-8046",
}

export enum PreferredHours {
    The1200Am1159PmPT = "12:00 am  11:59 pm PT",
}

export enum Specialization {
    Hospitalist = "Hospitalist",
    Na = "NA",
}

export enum Title {
    DR = "Dr.",
    TitleDR = "Dr",
}

export enum UpdatedBy {
    Mila = "MILA",
}
