import "../CSS/Header.css";
import logo from "../Images/Mila-Health-Logo.png";
import SearchIcon from "@mui/icons-material/Search";
import MenuIcon from '@mui/icons-material/Menu';
import HelpOutlineOutlinedIcon from "@mui/icons-material/HelpOutlineOutlined";
import SettingsOutlinedIcon from "@mui/icons-material/SettingsOutlined";
import PofilePic from "../Images/ProfileIcon.png";
import ProfileDropdownMenu from "../Components/ProfileDropdownMenu";
import { useRef, useState } from "react";

type DropDownProps = {
    UserName: string
    Designamtion: string
}

export const Header = (props: DropDownProps) => {
const [open, setOpen] = useState<boolean>(false);
const dropdownRef = useRef<HTMLDivElement>(null);
const handlDropDownFocus = (state: boolean) => {
    setOpen(!state)
};

const handleClickOutsideDropdown = (e:any) => {
    if(open && !dropdownRef.current?.contains(e.target as Node)) {
     setOpen(false)   
    }
}
window.addEventListener("click", handleClickOutsideDropdown)

    return( 
    <div className="header">
        <div className="leftelements">
            <div className="sidebaricon">
                <MenuIcon />
            </div>
            <img className="logo" src={logo} alt="Logo" />
            <div className="searchBar">
                <div className="Search SearchSize">
                    <SearchIcon />
                </div>
                <form>
                    <input type="text" placeholder="Search" />
                    <button type="submit"></button>
                </form>
            </div>
        </div>
        <div className="rightelements">
            <div className="help iconSize">
                <HelpOutlineOutlinedIcon />
            </div>
            <div className="settings iconSize">
                <SettingsOutlinedIcon />
            </div>            
            {/* Dropdown Menu */}
            <div className="containers" ref={dropdownRef}>
                <img className="ProfilePicIcon" alt="" src={PofilePic} onClick={(e) => handlDropDownFocus(open)} />
                <div>                    
                    {open && (<ProfileDropdownMenu UserName={props.UserName} Designamtion={props.Designamtion} />)}                     
                </div>
            </div>
        </div>
    </div> 
    )
}