import React, { useState } from 'react';
import PofilePic from "../Images/ProfileIcon.png";
import Edit from "../Images/edit.png";
import "../CSS/ProfileDropdown.css";
import Button from "../Components/Button";
import { useMsal } from '@azure/msal-react';
import PowerSettingsNewIcon from "@mui/icons-material/PowerSettingsNew";

type DropElements = {
  UserName: string;
  Designamtion: string;
};

export const ProfileDropdownMenu = (props: DropElements) => {
  const { instance } = useMsal();
  const [idToken, setIdToken] = useState('');

  const handleLogout = async () => {
    await instance.logoutRedirect();
    setIdToken("");
  };

  return (
    <div className="userdrop">
      <div className="nameCard">
        <img className="ProfilePic" alt="" src={PofilePic} />
        <img className="EditIcon" alt="" src={Edit} />
        <div className="NameSection">
          <b className="UserName">{props.UserName}</b>
          <div className="UserDesignation">{props.Designamtion}</div>
        </div>
      </div>
      <div className="rectangleDiv" />
      <div className="manageSection">
        <Button Label="Manage Provider" style="Default" icon="right" />
        <Button Label="Manage Clinic" style="Default" icon="right" />
        <button className="Alt" onClick={handleLogout}>Logout <PowerSettingsNewIcon className="transp"/></button>
      </div>
    </div>
  );
};

export default ProfileDropdownMenu;
