import React, { useEffect, useState } from 'react';
import "../CSS/Titlebar.css";
import Button from "./Button";
import { styled } from '@mui/material/styles';
import FormControlLabel from '@mui/material/FormControlLabel';
import Switch, { SwitchProps } from '@mui/material/Switch';
import SwapVertIcon from "@mui/icons-material/SwapVert";
import Tooltip from '@mui/material/Tooltip';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import GridDash from "./GridDash";
import ListDash from "./ListDash";
import { Case, Welcome } from './types';
import { useMsal } from '@azure/msal-react';
import { loginRequest } from '../Auth/authConfig';
import LoadingCard from "./LoadingCard";

interface Props {
  title: string
  back: boolean
  jsonData?: {
    cases: Case[];
  };
}

const theme = createTheme({
  typography: {
    allVariants: {
      fontFamily: 'Poppins',
    },
  },
});

const DasboardBody: React.FC<Props> = ({ title, back, jsonData }) => {
  const [data, setData] = useState<Welcome | null>(null);
  const [error, setError] = useState<string | null>(null);
  const { instance, accounts } = useMsal();
  const [activeButton, setActiveButton] = useState<'list' | 'grid'>('list'); // State to manage active button

  const toggleButton = (type: 'list' | 'grid') => {
    setActiveButton(type);
  };

  const getAccessToken = async () => {
    if (accounts.length > 0) {
      const request = {
        ...loginRequest,
        account: accounts[0]
      };

      try {
        const token = localStorage.getItem("token"); // Retrieve token from localStorage
        return token;
      } catch (error) {
        const token = localStorage.getItem("token"); // Retrieve token from localStorage
        console.error('Token acquisition failed:', error);
        return token;
      }
    }
    throw new Error('No accounts found');
  };
  
  useEffect(() => {
    const fetchData = async () => {
      const url = '/api/v1/cases?offset=0&limit=10&status=LAUNCHED';
      const token = await getAccessToken();

      try {
        const response = await fetch(url, {
          method: 'GET',
          headers: {
            'Accept': '*/*',
            'Authorization': `Bearer ${token}`
          }
        });
        if (!response.ok) {
          throw new Error('Network response was not ok');
        }
        const result: Welcome = await response.json();
        setData(result);
      } catch (error) {
        setError((error as Error).message);
      }
    };

    fetchData();
  }, []);

  return (
    <> 
      <div className="titlebar">
        {back ? <div className="Title"><Button style="Back" icon="back" />{title}</div> : <div className="Title">Welcome, {title}!</div> }          
        <div className="actions">
          {back && (
            <div className="toggleGRP">
              <ThemeProvider theme={theme}>
                <Tooltip title="Sort Cases by Date" placement="top" arrow>
                  <div className="togglespace">
                    <FormControlLabel control={<IOSSwitch sx={{ m: 1 }} defaultChecked />} label="Date" />
                    <SwapVertIcon className="sortHover" />
                  </div>
                </Tooltip>
              </ThemeProvider>
              <ThemeProvider theme={theme}>
                <Tooltip title="Sort Cases by Severity" placement="top" arrow>
                  <div className="togglespace">
                    <FormControlLabel control={<IOSSwitch sx={{ m: 1 }} />} label="Severity" />
                    <SwapVertIcon className="sortHover" />
                  </div>
                </Tooltip>
              </ThemeProvider>
              <ThemeProvider theme={theme}>
                <Tooltip title="Show Draft Cases" placement="top" arrow>
                  <div className="togglespace">
                    <FormControlLabel control={<IOSSwitch sx={{ m: 1 }} />} label="Draft" />
                  </div>
                </Tooltip>
              </ThemeProvider>
            </div>
          )}
          <div className="actionGRP">
            <Button style="Alt-Ghost" icon="snapshot" />
            <Button style="Alt-Ghost" icon="close" />
            <button className={activeButton === 'grid' ? 'activeButton' : ''} onClick={() => toggleButton('grid')} style={{border:"none", padding:"0"}}>
              <Button style="Alt-Ghost" icon='grid'/>
            </button>
            <button className={activeButton === 'list' ? 'activeButton' : ''} onClick={() => toggleButton('list')} style={{border:"none", padding:"0"}}>
              <Button style="Alt-Ghost" icon='list'/>
            </button>
            <Button Label="Add New Case" style="Default" icon="plus" />
          </div>
        </div>
      </div>
      <div className='loader'>
        {error && <p>Error: {error}</p>}
        <div id='grid' style={{ display: activeButton === 'grid' ? 'block' : 'none', width:'100%' }}>
          {data ? <GridDash jsonData={data} /> : <><LoadingCard Message={"Loading Data"} Helper={"Fetching the data from API."} /></>}
        </div>
        <div id='list' style={{ display: activeButton === 'list' ? 'block' : 'none', width:'100%' }}>
          {data ? <ListDash jsonData={data} /> : <><LoadingCard Message={"Loading Data"} Helper={"Fetching the data from API."} /></>}
        </div>
      </div>
    </> 
  );
};

const IOSSwitch = styled((props: SwitchProps) => (
  <Switch focusVisibleClassName=".Mui-focusVisible" disableRipple {...props} />
))(({ theme }) => ({
  width: 42,
  height: 26,
  padding: 0,
  '& .MuiSwitch-switchBase': {
    padding: 0,
    margin: 2,
    transitionDuration: '300ms',
    '&.Mui-checked': {
      transform: 'translateX(16px)',
      color: '#fff',
      '& + .MuiSwitch-track': {
        backgroundColor: theme.palette.mode === 'dark' ? '#2ECA45' : '#4fbc9c',
        opacity: 1,
        border: 0,
      },
      '&.Mui-disabled + .MuiSwitch-track': {
        opacity: 0.5,
      },
    },
    '&.Mui-focusVisible .MuiSwitch-thumb': {
      color: '#33cf4d',
      border: '6px solid #fff',
    },
    '&.Mui-disabled .MuiSwitch-thumb': {
      color:
        theme.palette.mode === 'light'
          ? theme.palette.grey[100]
          : theme.palette.grey[600],
    },
    '&.Mui-disabled + .MuiSwitch-track': {
      opacity: theme.palette.mode === 'light' ? 0.7 : 0.3,
    },
  },
  '& .MuiSwitch-thumb': {
    boxSizing: 'border-box',
    width: 22,
    height: 22,
  },
  '& .MuiSwitch-track': {
    borderRadius: 26 / 2,
    backgroundColor: theme.palette.mode === 'light' ? '#E9E9EA' : '#39393D',
    opacity: 1,
    transition: theme.transitions.create(['background-color'], {
      duration: 500,
    }),
  },
}));

export default DasboardBody;
