import "../CSS/ListDash.css";
import InProgress from "./InProgress";
import CaseNotSelected from "./CaseNotSelected";
import React, { useEffect, useState } from 'react';
import { useMsal } from '@azure/msal-react';
import { loginRequest } from '../Auth/authConfig';
import { Case, Welcome } from './types';
import CaseList from "./CasesList";
import LoadingCard from "./LoadingCard";

interface Props {
  jsonData?: {
      cases: Case[];
  };
}

const ListDash: React.FC<Props> = ({ jsonData }) => {
  const [data, setData] = useState<Welcome | null>(null);
  const [error, setError] = useState<string | null>(null);
  const { instance, accounts } = useMsal();

    const getAccessToken = async () => {
      if (accounts.length > 0) {
        try {
          const token = localStorage.getItem("token"); 
          return token;
        } catch (error) {
          const token = localStorage.getItem("token"); 
          return token;
        }
      }
      throw new Error('No accounts found');
    };
    
    useEffect(() => {
      const fetchData = async () => {
        const url = '/api/v1/cases?offset=0&limit=10&status=LAUNCHED';
        const token = await getAccessToken();
       console.log(token);
  
        try {
          const response = await fetch(url, {
            method: 'GET',
            headers: {
              'Accept': '*/*',
              'Authorization': `Bearer ${token}`
            }
          });
          if (!response.ok) {
            throw new Error('Network response was not ok');
          }
          const result: Welcome = await response.json();
          setData(result);
        } catch (error) {
          setError((error as Error).message);
        }
      };
      fetchData();
    }, []);

  return (
    <div className="body">
        <>
            {data ? <CaseList jsonData={data} /> : <><LoadingCard Message={"Almost Done"} Helper={"Please wait while we set up your dashboard."} /></> }
        </>
    </div>
  )
}

export default ListDash