import React, { useEffect, useState } from 'react';
import { Case, Welcome } from './types';
import { useMsal } from '@azure/msal-react';
import "../CSS/GridDash.css";
import ChecklistIcon from "@mui/icons-material/Checklist";
import CheckCircleIcon from "@mui/icons-material/CheckCircle";
import DraftsIcon from "@mui/icons-material/Drafts";
import Button from "./Button";
import InProgress from "./InProgress";
import Closed from "./Closed";

    interface Props {
        jsonData?: {
            cases: Case[];
        };
    }
  
    const GridDash: React.FC<Props> = ({ jsonData }) => { 
      const [data, setData] = useState<Welcome | null>(null);
      const [error, setError] = useState<string | null>(null);
      const { instance, accounts } = useMsal();
    
        const getAccessToken = async () => {
          if (accounts.length > 0) {
            try {
              const token = localStorage.getItem("token"); 
              return token;
            } catch (error) {
              const token = localStorage.getItem("token"); 
              return token;
            }
          }
          throw new Error('No accounts found');
        };
        
        useEffect(() => {
          const fetchData = async () => {
            const url = '/api/v1/cases?offset=0&limit=10&status=LAUNCHED';
            const token = await getAccessToken();
            // console.log(token);
      
            try {
              const response = await fetch(url, {
                method: 'GET',
                headers: {
                  'Accept': '*/*',
                  'Authorization': `Bearer ${token}`
                }
              });
              if (!response.ok) {
                throw new Error('Network response was not ok');
              }
              const result: Welcome = await response.json();
              setData(result);
            } catch (error) {
              setError((error as Error).message);
            }
          };
          fetchData();
        }, []);

    return(                        
    <div className="content">
        <div className="maxValue container">
            <div className="main-head">
                <div className="title">
                    <ChecklistIcon />
                    In Progress
                </div>
            </div>                    
            {error && <p>Error: {error}</p>}
            {data ? <InProgress jsonData={data} /> : <p>Loading case details...</p>}                
        </div>
        <div className="minValue container">
            <div className="main-head">
                <div className="title">
                    <DraftsIcon />Draft
                </div>
                <div><Button Label="" style="Round" icon="closed" /></div> 
            </div>                    
            <div className="cases">
                {/* <CaseCard firstName={"Test"}/>
                <CaseCard firstName={"Last"}/>
                <CaseCard firstName={"Name"}/> */}
            </div>
        </div>
        <div className="minValue container">
            <div className="main-head">
                <div className="title">
                    <CheckCircleIcon />Closed
                </div>
                <div><Button Label="" style="Round" icon="closed" /></div> 
            </div>                    
            {/* <EmptyCard /> */}
            <div className="cases">
                {error && <p>Error: {error}</p>}
                {data ? <Closed jsonData={data} /> : <p>Loading case details...</p>}
            </div>
        </div>
    </div>
 )
}
export default GridDash;